import { Box, Paper } from "@mui/material";
import { useNavigate } from "react-router-dom";
import Controls from "../../components/controls/Controls";
import Mensagens from "../../util/Mensagens";
import * as Cores from "../../util/cores";
import * as Icones from '../../util/icones';

export default function HomeMobile(props) {
    const { userDetails } = props
    const navigate = useNavigate();

    const handleRedirectLogin = () => {
        navigate("/login")
    }
    return (
        <Box>
            <Paper sx={{ background: Cores.COR_SECUNDARIA }}>
                {userDetails.id === 0 ?
                    <Controls.Button
                        startIcon={Icones.loginSecundario}
                        text={Mensagens.LOGIN}
                        onClick={() => handleRedirectLogin()}
                        variant='outlined'
                        color='secondary'
                        sx={{ width: '50%', marginTop: '0px', fontSize: '25px', background: 'white' }}
                    />
                :
                <Box> Usurio logado!</Box>
                }
            </Paper>
        </Box>
    )
}